@import './fonts';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $font-family-primary, 'Arial', sans-serif;
}

*::after, *::before {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

html {
  width: 100vw;
  overflow-x: hidden;
}